}

.backdrop {
  width: 100vw;
  height: 100vh;
  background-color: rgba(174, 181, 191, .416);
  position: fixed;
  top: 0;
  left: 0;
  overflow: hidden;
}

.backdrop.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

.modal {
  width: 500px;
  height: 600px;
  background-color: #fff;
  border-radius: 30px;
  padding: 20px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  box-shadow: 0 50px 100px -20px rgba(50, 50, 93, .25), 0 30px 60px -30px rgba(0, 0, 0, .3), inset 0 -2px 6px rgba(10, 37, 64, .35);
}

.close-modal {
  width: 70px;
  height: 30px;
  background-color: #313ef7;
  border: rgba(0, 0, 0, 0);
  border-radius: 50px;
  position: absolute;
  right: 20px;
}

.close-modal:hover, .close-modal:focus {
  color: #fff;
  background-color: #aeb5bf;
  box-shadow: 0 50px 100px -20px rgba(50, 50, 93, .25), 0 30px 60px -30px rgba(0, 0, 0, .3), inset 0 -2px 6px rgba(10, 37, 64, .35);
}

